import Vue from "vue";
import App from "./App.vue";
import "../registerServiceWorker";
import store from "../store";
import router from "../router";
import VeeValidate from "vee-validate";
import axios from "axios";
import NProgress from "nprogress";
import "nprogress/nprogress.css";
import { baseURL } from "../_helpers/utils";
//Internal stuff created by me
import { globalMethods } from "../_helpers/global-methods.js";

axios.withCredentials = true;
axios.interceptors.request.use(
  (config) => {
    store.state.isLoading = true;
    return config;
  },
  (error) => {
    store.commit("REQUEST_RESOLVED");
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  (response) => {
    store.commit("REQUEST_RESOLVED");
    return response;
  },
  (error) => {
    store.commit("REQUEST_RESOLVED");
    if (error.response.status === 401) {
      store.dispatch("logout").then(() => {
        if (location.href.indexOf("admin") > -1) {
          location.replace("/");
        }
      });
    }
    if (error.response.status == 500) {
      store.commit("SET_FLASH_MESSAGE", {
        type: "danger",
        message: "Error Occured. Please contact system administrator",
      });
    }
    return Promise.reject(error);
  }
);

Vue.prototype.$http = axios;
const token = localStorage.getItem("token");
if (token) {
  Vue.prototype.$http.defaults.headers.common["Authorization"] =
    "Bearer " + token;
}
Vue.prototype.$http.defaults.baseURL = baseURL;

const options = {
  latencyThreshold: 200,
  router: true,
  http: true,
};

Vue.use(NProgress, options);
Vue.use(VeeValidate);
Vue.mixin(globalMethods);

Vue.config.productionTip = false;

new Vue({
  store,
  router,
  render: (h) => h(App),
}).$mount("#app");
