<template>
  <div>
    <router-view />
    <loader
      v-if="$store.state.isLoading && $store.state.requestFlag === null"
    ></loader>
  </div>
</template>
<script>
import Loader from "@/components/Loader.vue";
export default {
  name: "app",
  components: {
    Loader,
  },
  data: () => ({
    isMoboEnv: false,
    orderCode: false,
  }),
  created() {
    this.checkMobileEnv();
    this.getSettings();
  },
  methods: {
    checkMobileEnv() {
      if (matchMedia) {
        const mq = window.matchMedia("(max-width: 768px)");
        this.isMoboEnv = mq.matches;
        try {
          // Chrome & Firefox
          mq.addEventListener("change", () => {
            this.isMoboEnv = mq.matches;
          });
        } catch (e1) {
          try {
            // Safari
            mq.addListener(() => {
              this.isMoboEnv = mq.matches;
            });
          } catch (e2) {
            console.error(e2);
          }
        }
        //this.WidthChange(mq);
      }
    },
    WidthChange(mq) {
      this.isMoboEnv = mq.matches;
    },
  },
};
</script>
